header {
    position: relative;
    height: 70px;
    padding-top: 10px;
    margin-bottom: 20px;
    color: #ffffff;
    background: #0767c8;
    background: -webkit-linear-gradient(top, #086ed5, #055db5);
    background: -webkit-gradient(linear, left top, left bottom, from(#086ed5), to(#055db5));
    background: linear-gradient(to bottom, #086ed5, #055db5);
    border-bottom: 1px solid #044e97;
    box-shadow: 0 1px 2px rgba(0, 0, 0, 0.25), inset 0 1px 0 rgba(255, 255, 255, 0.15);
    font-family: 'Helvetica Neue', Helvetica, Arial, sans-serif;
}

header a {
    color: rgba(255, 255, 255, 0.5);
    -webkit-transition: 0.2s ease;
    transition: 0.2s ease;
}

header a:hover {
    color: rgba(69, 157, 245, 0.9);
}

