header .wrapper {
    width: 1140px;
    margin: 0 auto;
    line-height: 52px;
}

header .wrapper .brand {
    font-weight: 600;
    font-size: 26px;
    color: #ffffff;
}

header .wrapper small {
    font-family: Merriweather-Light, -apple-system, Verdana, "PingFang SC", "Helvetica Neue", Arial, "Hiragino Sans GB", "Microsoft YaHei", "WenQuanYi Micro Hei", sans-serif;
    margin-left: 26px;
    font-size: 14px;
    color: #bdd3ea;
}

header .wrapper .menu {
    display: none;
}

header .wrapper nav {
    float: right;
}

header .wrapper nav ul {
    font-size: 0;
    margin: 0;
    padding: 0;
}

header .wrapper nav ul li {
    list-style: none;
    font-size: 14px;
    display: inline-block;
    -webkit-transition: 0.2s ease;
    transition: 0.2s ease;
}

header .wrapper nav ul li a {
    display: inline-block;
    padding: 0 15px;
    height: 30px;
    line-height: 30px;
}

header .wrapper nav ul li a:hover {
    color: #ffffff;
}

header .wrapper nav ul li a i {
    margin-right: 5px;
}

header .wrapper nav ul li>.active {
    color: #ffffff;
    font-weight: 700;
}

@media screen and (max-width: 1200px) and (min-width: 770px) {
    header .wrapper {
        width: 90%;
        line-height: 50px;
    }

    header .wrapper small {
        margin-left: 15px;
        font-size: 12px;
    }

    header .wrapper .menu {
        display: none;
    }

    header .wrapper nav ul li a {
        padding: 0 10px;
        height: 30px;
        line-height: 30px;
    }

    header .wrapper nav ul li a i {
        margin-right: 4px;
    }
}

@media screen and (max-width: 770px) {
    header {
        margin-bottom: 15px;
    }

    header .wrapper {
        width: 95%;
        margin: 0 auto;
        line-height: 48px;
        position: relative;
        z-index: 101;
    }

    header .wrapper .brand {
        margin-left: 10px;
        font-size: 20px;
    }

    header .wrapper small {
        display: none;
    }

    header .wrapper .menu {
        display: inline-block;
        position: absolute;
        z-index: -102;
        right: 0;
        top: 10px;
        cursor: pointer;
        padding: 5px 10px;
        background: #104d8a;
        border: 1px solid #0a6ccc;
        border-radius: 4px;
        color: #f8f8f2;
        font-size: 17px;
        -webkit-transition: 0.4s ease;
        transition: 0.4s ease;
    }

    header .wrapper>.active {
        background: #1480ea;
        color: #fff;
    }

    header .wrapper .nav-show {
        visibility: visible;
        -webkit-transform: scale(1);
                transform: scale(1);
        opacity: 1;
    }

    header .wrapper nav {
        position: absolute;
        z-index: 100;
        right: 0;
        top: 60px;
        text-align: center;
        visibility: hidden;
        -webkit-transform-origin: 100% 0%;
                transform-origin: 100% 0%;
        -webkit-transform: scale(0.1);
                transform: scale(0.1);
        opacity: 0;
        -webkit-transition: 0.2s cubic-bezier(0.5, 0.1, 0.51, 1.38);
        transition: 0.2s cubic-bezier(0.5, 0.1, 0.51, 1.38);
    }

    header .wrapper nav ul {
        border-radius: 0 0 4px;
        background: rgba(51, 51, 51, 0.72);
    }

    header .wrapper nav ul li {
        font-size: 14px;
        display: block;
        border-bottom: 1px solid rgba(0, 0, 0, 0.08);
    }

    header .wrapper nav ul li a {
        color: white;
        display: block;
        padding: 2px 28px;
        height: 40px;
        line-height: 40px;
    }

    header .wrapper nav ul li:last-of-type {
        border: none;
    }

    header .wrapper nav ul li>.active {
        background-color: black;
        border-radius: 0;
        box-shadow: none;
        border: none;
    }
}

