.container {
    width: 80%;
    *zoom: 1;
    margin-right: auto;
    margin-left: auto
}

@media screen and (max-width: 1200px) and (min-width: 770px) {
    .container {
        width: 90%;
        *zoom: 1;
        margin-right: auto;
        margin-left: auto
    }
}

@media screen and (max-width: 770px)  {
    .container {
        width: 100%;
        *zoom: 1;
        margin-right: auto;
        margin-left: auto
    }
}
.container:before, .container:after {
    content: " ";
    display: table
}

.container:after {
    clear: both
}

.clear:after {
    content: ".";
    display: block;
    height: 0;
    clear: both;
    overflow: hidden;
}

